import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Preloader from '../../components/Preloader/Preloader';
import './Registration.scss';

function Registraton({ setShowInfo, isLoading, setIsLoading }) {
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [loginDirty, setLoginDirty] = useState(false);
    const [passwordDirty, setPasswordDirty] = useState(false);
    const [confirmPasswordDirty, setConfirmPasswordDirty] = useState(false);
    const [loginError, setLoginError] = useState(
        '   '
    );
    const [passwordError, setPasswordError] = useState(
        '   '
    );
    const [confirmPasswordError, setConfirmPasswordError] = useState(
        '   '
    );
    const [formValid, setFormValid] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        if (loginError || passwordError || confirmPasswordError) {
            setFormValid(false);
        } else {
            setFormValid(true);
        }
    }, [loginError, passwordError, confirmPasswordError]);

    const loginHandler = (e) => {
        setLogin(e.target.value);
        if (e.target.value === ' ') {
            setLoginError(' ');
        } else {
            setLoginError('');
        }
    };

    const passwordHandler = (e) => {
        setPassword(e.target.value);
        if (e.target.value === ' ') {
            setPasswordError(' ');
        } else {
            setPasswordError('');
        }
    };

    const confirmPasswordHandler = (e) => {
        setConfirmPassword(e.target.value);
        if (e.target.value !== password) {
            setConfirmPasswordError('  ');
        } else {
            setConfirmPasswordError('');
        }
    };

    const blurHandler = (e) => {
        switch (e.target.name) {
            case 'login':
                setLoginDirty(true);
                break;
            case 'password':
                setPasswordDirty(true);
                break;
            case 'confirmPassword':
                setConfirmPasswordDirty(true);
                break;
        }
    };

    const onSubmitHandler = async (event) => {
        try {
            event.preventDefault();
            setIsLoading(true);
            let formData = new FormData();
            formData.append('login', login);
            formData.append('password', password);

            await axios({
                method: 'get',
                baseURL: 'http://f0883110.xsph.ru',
                url: '/reg.php?login=' + login,
                data: formData,
            }).then((response) => {
                if (response.data === null) {
                    try {
                        axios({
                            method: 'post',
                            baseURL: 'http://f0883110.xsph.ru',
                            url: '/reg.php?login=' + login,
                            data: formData,
                            config: {
                                headers: {
                                    'Content-type': 'multipart/form-data',
                                },
                            },
                        });
                    } catch (error) {
                        alert('   ');
                        console.error(error);
                    }

                    setShowInfo(true);
                    navigate('/login');
                } else {
                    setLoginError(
                        '     '
                    );
                }
            });
        } catch (error) {
            console.error(error);
        }
        setIsLoading(false);
    };

    return (
        <main className="registration">
            {isLoading && <Preloader />}
            <h3 className="title-3"></h3>
            <form onSubmit={onSubmitHandler} className="registration__form">
                <label htmlFor="login"></label>
                {loginDirty && loginError && (
                    <div className="registration__form-error">{loginError}</div>
                )}
                <input
                    onBlur={(e) => blurHandler(e)}
                    onChange={(e) => loginHandler(e)}
                    className={loginDirty && loginError ? 'error' : ''}
                    type="text"
                    placeholder=" "
                    name="login"
                    value={login}
                />
                <label htmlFor="password"></label>
                {passwordDirty && passwordError && (
                    <div className="registration__form-error">
                        {passwordError}
                    </div>
                )}
                <input
                    onBlur={(e) => blurHandler(e)}
                    onChange={(e) => passwordHandler(e)}
                    className={passwordDirty && passwordError ? 'error' : ''}
                    type="password"
                    placeholder=" "
                    name="password"
                    value={password}
                />
                <label htmlFor="confirmPassword"> </label>
                {confirmPasswordDirty && confirmPasswordError && (
                    <div className="registration__form-error">
                        {confirmPasswordError}
                    </div>
                )}
                <input
                    onBlur={(e) => blurHandler(e)}
                    onChange={(e) => confirmPasswordHandler(e)}
                    className={
                        confirmPasswordDirty && confirmPasswordError
                            ? 'error'
                            : ''
                    }
                    type="password"
                    placeholder="   "
                    name="confirmPassword"
                    value={confirmPassword}
                />
                <button disabled={!formValid} type="submit">
                    
                </button>
            </form>
        </main>
    );
}

export default Registraton;
